import { Injectable } from '@angular/core';
import { HttpRequest, HttpResponse, HttpHandler, HttpEvent, HttpInterceptor, HTTP_INTERCEPTORS } from '@angular/common/http';
import { Observable, of, throwError } from 'rxjs';
import { delay, mergeMap, materialize, dematerialize } from 'rxjs/operators';

type Tarticles = {
  name: string,
  articles: {
    title: string,
    content: string
  }[]
}[];

@Injectable({
  providedIn: 'root'
})
class MockServerService implements HttpInterceptor {

  nuevoArticulo(article, level: string): Observable<HttpEvent<any>> {
    let articulos: Tarticles = JSON.parse(localStorage.getItem('articulos')) || [];

    if (articulos.length == 0) {
      articulos =
        [
          { name: 'Nivel Basico', articles: [article] },
          { name: 'Nivel Medio', articles: [] },
          { name: 'Nivel Avanzado', articles: [] }
        ];
    } else {
      articulos[articulos.findIndex((x) => x.name == level)].articles.push(article);
    }

    localStorage.setItem('articulos', JSON.stringify(articulos));

    console.log(articulos);

    return of(new HttpResponse({
      status: 200,
      body: article
    }))
  }

  todosLosNiveles(): Observable<HttpEvent<any>> {
    let articulos: Tarticles = JSON.parse(localStorage.getItem('articulos')) || [];

    if (articulos.length == 0) {
      return of(new HttpResponse({
        status: 200,
        body: [
          { name: 'Nivel Basico', articles: [] },
          { name: 'Nivel Medio', articles: [] },
          { name: 'Nivel Avanzado', articles: []}
        ]
      }));
    } else {
      return of(new HttpResponse({
        status: 200,
        body: articulos
      }));
    }
  }

  enrutar({ url, body }): Observable<HttpEvent<any>> {
    if (new RegExp('^levels/.+/lessons$').test(url)) {
      return this.nuevoArticulo(body, url.split("/")[1]);
    } else if (new RegExp('^levels$').test(url)) {
      return this.todosLosNiveles();
    } else {

    }
  }

  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    console.log(req);
    return this.enrutar(req);
  };

  constructor() { }
}

export const mockServerService = { provide: HTTP_INTERCEPTORS, useClass: MockServerService, multi: true }