import { Component, OnInit, AfterViewInit, ViewChild, ElementRef } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { switchMap, map } from 'rxjs/operators';
import { Observable } from 'rxjs';

import { ToolbarService, LinkService, ImageService, HtmlEditorService ,RichTextEditorComponent} from '@syncfusion/ej2-angular-richtexteditor';
import { ApiService } from "../api.service";

@Component({
  selector: 'app-lesson-editor',
  templateUrl: './lesson-editor.component.html',
  styleUrls: ['./lesson-editor.component.css'],
  providers: [ToolbarService, LinkService, ImageService, HtmlEditorService, ApiService]//services
})
export class LessonEditorComponent implements OnInit ,AfterViewInit {

  @ViewChild('title') titleElement: ElementRef;
  public title: string;

  @ViewChild('editor') editor: RichTextEditorComponent;
  public tools: object = {
    type: 'MultiRow',
    items: [
      'Bold', 'Italic', 'Underline', 'StrikeThrough', '|',
      'FontName', 'FontSize', 'FontColor', 'BackgroundColor', '|',
      'SubScript', 'SuperScript', '|',
      'LowerCase', 'UpperCase', '|',
      'Formats', 'Alignments', '|', 'OrderedList', 'UnorderedList', '|',
      'Indent', 'Outdent', '|', 'CreateLink',
      'Image', '|', 'ClearFormat', 'Print', 'SourceCode', '|', 'FullScreen']};
  public iframe: object = { enable: true };
  public height: number = 500;

  public level:string;

  constructor(private route: ActivatedRoute, private router: Router, private api:ApiService){ 
  }

  ngAfterViewInit() {}

  ngOnInit() {
    this.route.paramMap.pipe(
      map((params:ParamMap)=>params.get('level'))
    ).subscribe((level:string)=>{
      this.level = level;
    });
  }

  saveLesson(title: string) {
    this.api.saveLesson({ title:this.titleElement.nativeElement.value,
                          content:this.editor.value
                        },this.level)
    .subscribe((res)=>{
      console.log("enviar anuncio");
      this.api.announceNewLesson("x");
    });
  }
}

