import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { Observable,Subject } from 'rxjs';
import { switchMap, map, mergeMap, merge } from 'rxjs/operators';

enum EndPoints {
  saveLesson = "levels/:level/lessons",
  allLevels = "levels"
}

type lesson = {
    title: string;
    content: string;
}

type Tarticles = {
  name:string,
  articles:{
    title: string,
    content: string
  }[]
}[];

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  constructor(private http: HttpClient) { 
    
   }

  public newLessonSource = new Subject<string>();

  saveLesson(lesson:lesson,level:string):Observable<HttpResponse<lesson>>{
    return this.http.post<lesson>(EndPoints.saveLesson.replace(':level',level), lesson, { observe: 'response' });
  }

  announceNewLesson(x:string){
    
    console.log("anunciando");
    this.newLessonSource.next(x);
  }

  getAllLevels():Observable<HttpResponse<Tarticles>>{
    return this.http.get<Tarticles>(EndPoints.allLevels, { observe: 'response' });
  }
}